import { animate, state, style, transition, trigger } from '@angular/animations';
import { AfterViewInit, Component, ElementRef, EventEmitter, Input, OnInit, Output, Renderer, ViewChild} from '@angular/core';
@Component({
  selector: 'amexio-accordion-tab',
  template: `
    <button #btn role="button" id="{{componentId}}" tabindex="0" (click)="emitEvent()" [attr.aria-expanded]="active"
      (keyup.enter)="onEnterClick()" (keyup.arrowdown)="onArrowDown()" (keyup.arrowup)="onArrowUp()" class="{{isTransparent ? 'accordion-transparent' : 'accordion'}} {{active ? 'active-accordion' : ''}} {{disabled ? 'accordion-disable' : ''}}"
      (mouseover)="hover=true" (mouseleave)="hover=false" [ngStyle]="{color: hover==true ? bgColor : color, backgroundColor: hover==true  ? color : bgColor}">
      <div style="float: left;" *ngIf="leftIcon">
        <em class="fa {{leftIcon}}"></em>
      </div>
      <ng-container>
        {{header}}
        <div style="float: right" role="img" aria-label="close push button" tabindex="0">
          <ng-content select="amexio-accordion-header">&nbsp;</ng-content> &nbsp;
          <em *ngIf="!angleIcon" class="fa" [ngClass]="{'fa-plus' : !active,'fa-minus' : active}" aria-hidden="true"></em>
          <em *ngIf="angleIcon" class="fa" [ngClass]="{'fa-angle-down' : !active,'fa-angle-up' : active}" aria-hidden="true"></em>
        </div>
      </ng-container>
    </button>
    <div role="region" attr.aria-labelledby="{{componentId}}" tabindex="0" [@accordionState]="active ? 'visible':'hidden'"
      class="panel">
      <ng-content></ng-content>
    </div>
  `,
  animations: [
    trigger('accordionState', [
      state('hidden', style({
        display: 'none',
        height: '0px',
      })),
      state('visible', style({
        display: 'block',
        height: '*',
      })),
      transition('*=>hidden', animate('0ms')),
      transition('*=>visible', animate('200ms')),
    ]),
  ],
})
export class AmexioAccordionTabComponent implements OnInit {
  /*
  Properties
  name : header
  datatype : any
  version : 4.0 onwards
  default :
  description : User can bind title for accordion tab.*/
  @Input() header: any;
  /*
  Properties
  name : active
  datatype : boolean
  version : 4.0 onwards
  default : false
  description : user can Set true for specific accordian open as default
  */
  @Input() active: boolean;
  /*
  Events
  name : emittedEvent
  datatype :none
  version : none
  default : none
  description : Fires the on accordion pane click event.
  */
  @Output() emittedEvent = new EventEmitter();
  /*
  Properties
  name : left-icon
  datatype : string
  version : 4.0 onwards
  default :
  description : places the icon on left
  */
  @Input('left-icon') leftIcon: string;
  /*
  Properties
  name : angle-icon
  datatype : boolean
  version : 4.0 onwards
  default : false
  description : Can use Angle Icons instead of default plus/minus icons
  */
  @Input('angle-icon') angleIcon: boolean;
  /*
  Properties
  name : disabled
  datatype : boolean
  version : 4.0 onwards
  default : false
  description : 	Disabled specific panes
  */
  @Input('disabled') disabled: boolean;
  @Input('data') data: any;
  @ViewChild('btn') btn: ElementRef;
  isTransparent: boolean;
  currentstate: string;
  expanded = false;
  componentId: any;
  isSelected = false;
  bgColor: string;
  color: string;
  hover: boolean;

  constructor() {
    this.currentstate = 'hidden';
  }

  ngOnInit() {
    this.componentId = '' + Math.floor(Math.random() * 1000 + 999);
    this.emittedEvent.emit({ keydown: true, current: this });

  }
  emitEvent() {
    if (!this.disabled) {
      this.active = !this.active;
      this.emittedEvent.emit({ keydown: false, current: this });
    }
    if (this.active) {
      this.currentstate = 'visible';
    } else {
      this.currentstate = 'hidden';
    }

  }
  onEnterClick() {
    this.expanded = !this.expanded;
  }
  onArrowDown() {
    this.emittedEvent.emit({ keydown: true, current: this });
  }
  onArrowUp() {
      this.emittedEvent.emit({ keyup: true, current: this });
  }

}
